const ADD_LOCATION = 'ADD-LOCATION';
const UPDATE_NEW_LOCATION_TEXT = 'UPDATE-NEW-LOCATION-TEXT';

let store = {
    _state: {
        OrderPage: {
            StepItem: [
                {id: 1, name: 'Местоположение'},
                {id: 2, name: 'Модель'},
                {id: 3, name: 'Дополнительно'},
                {id: 4, name: 'Итого'}
            ],
            Location: [],
            StepOutputData : [
                {id: 1, name: 'Пункт выдачи', item:' '},
                {id: 2, name: 'Модель', item:'Hyndai, i30 N'},
                {id: 3, name: 'Длительность аренды', item:'1д 2ч'},
                {id: 4, name: 'Тариф', item:'На сутки'},
                {id: 5, name: 'Полный бак', item:'Да'}
            ]
        }
    },
    getState() {
        return this._state;
    },
    renderEntireTree() {
        console.log('State ');
    },
    subscribe (observer) {
        this.renderEntireTree = observer;
    },
    dispatch(action) {
        if (action.type === ADD_LOCATION) {
            let newLocation = {
                id: 6, 
                name: 'Модель', 
                item: this._state.OrderPage.newLocationText
            }
            this._state.OrderPage.StepOutputData.push(newLocation);
            this._state.OrderPage.newLocationText = ''; 
            this.renderEntireTree(this._state);
        }
        else if (action.type === UPDATE_NEW_LOCATION_TEXT) {
            this._state.OrderPage.newLocationText = action.newText;
            this.renderEntireTree(this._state);
        }
    }
}

export const addLocationActionCreator = () =>  ({ type: ADD_LOCATION });

export const updateNewLocationText = (text) => 
 ({type: UPDATE_NEW_LOCATION_TEXT, newText: text});
     


export default store;



