import React from "react";
import { Route } from "react-router-dom";
import './App.css';
import MainPage from './Pages/MainPage.js';
import OrderPage from './Pages/OrderPage.js'
import { addLocation } from "./Redux/state";


const App = (props) => {
  return(
      <div className="App">
        <Route exact path="/" render={ () => <MainPage />} />
        <Route path="/order" render={ () => <OrderPage OrderPage={props.state.OrderPage} dispatch={props.dispatch} /> } />
     </div>
  )
}

export default App;

